.home{
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
    background-color:lightgray ;

}

.home__row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.home__carousel{
    z-index: -1 !important;
    margin-bottom: -200px;

}

.home__image
{
    width: 100%;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0.4));
    
}