.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky; /*to stick even if scrolled*/
    top: 0;
    z-index: 100;
}

.header__logo{
    width: 100px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 18px;
}

.header__searchIcon{
    background-color: #cd9042;
    padding: 5px;
    height: 22px !important;
}

.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header__search{
    display: flex; /*so that bar and serach icon be together*/
    flex: 1; /*to spread out*/
}

.header__nav{
    display: flex;
    justify-content: space-evenly;
}

.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__optionLineOne{
    font-size: 10px;

}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
    
}
.header__basketcount{
    margin-right: 10px;
    margin-left: 10px;
}
.header__link{
    color: white;
    text-decoration: none;
}

.header__optionBasket{
    display: flex;
    align-items: center;
}



